import React from 'react';
import axios from 'axios';

import CustomButton from '../../components/CustomButton/custom-button.component';
import UserRow from '../../components/User/row.component';

import './user.style.scss';

class UserPage extends React.Component {
	constructor(props) {
        super();

        this.state = {
        	users: []
        }
    }

    componentDidMount() {
		this.getUsers();
	}

	getUsers() {
		axios({ 
			method: 'get',
			url: process.env.REACT_APP_APIURL+'/getUsers'
		})
		.then(rsp => {
			const response = rsp.data;
			if( response.success ) {
				this.setState({
					users: response.users
				});
			}
			else{

			}
		})
		.catch(error => {
			console.log(error)
		});	
	}

	deletePrompt = (userid) => {
		console.log(userid)
		const approve = window.confirm('Are you sure you want to delete the selected record?');
		if( approve ) {
			axios({ 
				method: 'delete',
				url: process.env.REACT_APP_APIURL+'/user/'+userid
			})
			.then(rsp => {
				const response = rsp.data;
				if( response.success ) {
					this.getUsers();
				}
				else{

				}
			})
			.catch(error => {
				console.log(error)
			});
		}
	}

	render() {
		const { history } = this.props;
        return (
        	<div className="page user-page">
            	<h2>Users</h2>
				<div className="user-header">
		            <div className="header-block">
		                <span>Username</span>  
		            </div>
		            <div className="header-block">
		                <span>Fullname</span>  
		            </div>
		            <div className="header-block">
		                <span>Email</span>  
		            </div>
		            <div className="header-block">
		                <span>Mobile</span>  
		            </div>
		            <div className="header-block">
		                <span>Date Modified</span>  
		            </div>
		            <div className="header-block">
		                <span>Action</span>  
		            </div>
		        </div>
		        {
		        	this.state.users.map(user => {
		        		return(
		        			<UserRow key={user.userid} {...user}  deletePrompt={() => this.deletePrompt(user.userid)}/>
		        		)
		        	})
		        }

		        <br/>
		        <div className="flexdiv">
		        	<CustomButton onClick={() => history.push('/user/new')}>New User</CustomButton>
		        </div>
            </div>
        )
    }
}

export default UserPage;