import React from 'react';
import { Link } from 'react-router-dom';
import './row.style.scss';

const UserRow = ({userid, username, lastname, firstname, email, mobile, datemodified, deletePrompt}) => (
	<div className="row-container">
		<div className="info-block">
            <span>{username}</span>  
        </div>
        <div className="info-block">
            <span>{firstname} {lastname}</span>  
        </div>
        <div className="info-block">
            <span>{email}</span>  
        </div>
        <div className="info-block">
            <span>{mobile}</span>  
        </div>
        <div className="info-block">
            <span>{datemodified}</span>  
        </div>
        <div className="info-block">
            <span><Link to={'/user/'+userid} className="option">View</Link></span> <span> <a href="#" onClick={deletePrompt} className="option">Remove</a></span>  
        </div>
	</div>
)

export default UserRow;