import React from 'react';
import axios from 'axios';

import CustomButton from '../../components/CustomButton/custom-button.component';
import FormInput from '../../components/FormInput/form-input.component';

import './detail.style.scss';

class ViewUserPage extends React.Component {
	constructor(props) {
		super(props);
		
		this.state = {
			username: '',
			firstname: '',
			lastname: '',
			email: '',
			mobile: '',
			error: false,
			error_message: ''
		}
	}

	componentDidMount() {
		this.getUser();
	}

	getUser() {
		axios({ 
			method: 'get',
			url: process.env.REACT_APP_APIURL+this.props.match.url
		})
		.then(rsp => {
			const response = rsp.data;
			if( response.success ) {
				const user =  response.user;
				this.setState({
					username: user.username,
					firstname: user.firstname,
					lastname: user.lastname,
					email: user.email,
					mobile: user.mobile
				});
			}
			else{

			}
		})
		.catch(error => {
			console.log(error)
		});	
	}

	handleSubmit = async event => {
		console.log('fire')
        event.preventDefault();
		this.setState({
			error: false,
			error_message: ''
		}, () => {
			axios({ 
			  method: 'put',
			  url: process.env.REACT_APP_APIURL+this.props.match.url,
			  data: {
			  	firstname: this.state.firstname,
				lastname: this.state.lastname,
				email: this.state.email,
				mobile: this.state.mobile
			  }
			})
			.then(rsp => {
				var response = rsp.data;
				if( response.success ) {
					this.props.history.goBack()
				}
				else{
					this.setState({
						error: true,
						error_message: response.message
					});
				}
			})
			.catch(error => {
				console.log(error);
				this.setState({
					error: true,
					error_message: 'Something went wrong'
				});
			});	
		});
			
    }

    handleChange = event => {
        const {name, value} = event.target;
        this.setState({ [name]: value })
    }

	render() {
		
		return(
			<div className="page newuser-page">
				<h2>View and Update User</h2>
				{
					this.state.error ?
					<div className="error-message">{this.state.error_message }</div>
					: ''
				}
				<form onSubmit={this.handleSubmit}>
                    <FormInput name="username" type="text" value={this.state.username} required readOnly label="Username" />
                    <FormInput name="firstname" type="text" value={this.state.firstname} required onChange={this.handleChange} label="Firstname" />
                    <FormInput name="lastname" type="text" value={this.state.lastname} required onChange={this.handleChange} label="Lastname" />
                    <FormInput name="email" type="text" value={this.state.email} required onChange={this.handleChange} label="Email" />
                    <FormInput name="mobile" type="text" value={this.state.mobile} required onChange={this.handleChange} label="Mobile" />

                    <div className="flexdiv">
                    	<CustomButton type="button" cancel={true} onClick={() => this.props.history.goBack()}>Cancel</CustomButton>
						<CustomButton type="Submit">Update User</CustomButton>    
                    </div>
                </form>

			</div>
		)
	}
}

export default ViewUserPage;