import React from 'react';
import { Route, Switch } from 'react-router-dom';

import UserPage from './pages/user/user.component';
import NewUserPage from './pages/user/new.component';
import ViewUserPage from './pages/user/detail.component';

import './App.css';

function App() {
  return (
    <div className="App">
      <Switch>
        <Route exact path='/' component={UserPage} />
        <Route exact path='/user/new' component={NewUserPage} />
        <Route exact path='/user/:userid' component={ViewUserPage} />
      </Switch>
    </div>
  );
}

export default App;
